import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/login.css';

const AddUser = () => {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [contact, setContact] = useState('');
  const [password, setPassword] = useState('');
  let navigate = useNavigate();

  // Reading form inputs
  const handleSubmit = (e) => {
    e.preventDefault();
    const postData = {
      email,
      contact_number: contact,
      first_name: firstName,
      last_name: lastName,
      password,
    };

    fetch('/api/addUser', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(postData),
    }).then((response) => {
      if (response.status === 201) {
        // TODO: Redirect to EUS
        console.log('Successful sign up, redirect to EUS');
        alert('User successfully added');
        navigate('/equipmentUtilisationSnapshot');
        return;
      } else {
        alert('Unsuccessful!');
      }
    });
  };

  return (
    <div className='row loginPage'>
      <div className='loginContainer col-12'>
        <div className='card offset-4 p-4 px-6'>
          <h2>Add User</h2>
          <p>Please fill in your relevant details to add a user for FIRC!</p>
          <form onSubmit={handleSubmit}>
            <div className='row level'>
              <div className='col-6'>
                <div className='input-control w-100p'>
                  <input
                    type='text'
                    className='input-contains-icon'
                    placeholder='First Name'
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                    required
                  />
                  <span className='icon'>
                    <i className='fas fa-user'></i>
                  </span>
                </div>
              </div>
              <div className='col-6'>
                <div className='input-control w-100p'>
                  <input
                    type='text'
                    className='input-contains-icon'
                    placeholder='Last Name'
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                    required
                  />
                  <span className='icon'>
                    <i className='fas fa-user'></i>
                  </span>
                </div>
              </div>
            </div>
            <div className='row level'>
              <div className='col-6'>
                <div className='input-control w-100p'>
                  <input
                    type='email'
                    className='input-contains-icon'
                    placeholder='Email'
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                  />
                  <span className='icon'>
                    <i className='fas fa-envelope'></i>
                  </span>
                </div>
              </div>
              <div className='col-6'>
                <div className='input-control w-100p'>
                  <input
                    type='tel'
                    className='input-contains-icon'
                    placeholder='Contact'
                    value={contact}
                    onChange={(e) => setContact(e.target.value)}
                    required
                  />
                  <span className='icon'>
                    <i className='fas fa-phone'></i>
                  </span>
                </div>
              </div>
            </div>
            <div className='row level px-1'>
              <div className='input-control w-100p'>
                <input
                  type='password'
                  className='input-contains-icon'
                  placeholder='Password'
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
                <span className='icon'>
                  <i className='fas fa-key'></i>
                </span>
              </div>
            </div>
            <button className='btn-danger mt-2 mr-1 u-pull-right'>
              CREATE
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AddUser;
